@import url(https://fonts.googleapis.com/css2?family=Akronim&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Akronim", cursive;
}

.container {
  width: 1400px;
  margin: 0 auto;
}

h2 {
  margin: 0;
  font-size: 30px;
}

@media only screen and (max-width: 1500px) {
  .container {
    width: 90%;
  }
}

.single__card {
  border: 3px solid skyblue;
  border-radius: 10px;
  background-color: #daf2fc;
}

.single__card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.single__card h2 {
  padding: 5px 20px;
  text-align: center;
}

.spinner__container {
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  text-align: center;
  grid-column: span 4;
}

.spinner__grid {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.spinner__grid div {
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: red;
  animation: spinner 1.2s linear infinite;
}

.spinner__grid div:nth-child(1) {
  top: 8px;
  left: 8px;
  animation-delay: 0s;
}

.spinner__grid div:nth-child(2) {
  top: 8px;
  left: 32px;
  animation-delay: -0.4s;
}

.spinner__grid div:nth-child(3) {
  top: 8px;
  left: 56px;
  animation-delay: -0.8s;
}

.spinner__grid div:nth-child(4) {
  top: 32px;
  left: 8px;
  animation-delay: -0.4s;
}

.spinner__grid div:nth-child(5) {
  top: 32px;
  left: 32px;
  animation-delay: -0.8s;
}

.spinner__grid div:nth-child(6) {
  top: 32px;
  left: 56px;
  animation-delay: -1.2s;
}

.spinner__grid div:nth-child(7) {
  top: 56px;
  left: 8px;
  animation-delay: -0.8s;
}

.spinner__grid div:nth-child(8) {
  top: 56px;
  left: 32px;
  animation-delay: -1.2s;
}

.spinner__grid div:nth-child(9) {
  top: 56px;
  left: 56px;
  animation-delay: -1.6s;
}

@keyframes spinner {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.country__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px 20px;
  gap: 40px 20px;
  margin-top: 80px;
}

@media only screen and (max-width: 991px) {
  .country__container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 500px) {
  .country__container {
    grid-template-columns: repeat(1, 1fr);
  }
}

nav {
  position: fixed;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  background-color: skyblue;
}

